import Vue from 'vue'
import VueRouter from 'vue-router'
import app from './App'
import hello from './components/Home-page'
import Creator from './components/Creator-Page'
import HowWork from './components/How-itWork'

import './assets/style.sass'
import '../node_modules/bootstrap/scss/bootstrap.scss'
import VueCarousel from 'vue-carousel'
Vue.use(VueRouter)
Vue.use(VueCarousel)

const router = new VueRouter({
  mode: 'history',
  routes: [{
		path: '/',
		component: hello
	}, {
		path: '/creator',
		component: Creator
  }, {
		path: '/how-it-work',
		component: HowWork
	}, {
    path: '*',
    redirect: '/'
  }]
})

new Vue({
  el: '#app',
  router,
  render: h => h(app)
})



